.container {
    background: url(../public/Mohnwiese2.jpg);
    height: 100vh;
    background-position: center;
    background-size: cover;
}

.cardContainer {
    height: calc(100% - 85px);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.cardContent {
    background-color: #fdfdfd;
    padding: 5rem 8rem;
    border-radius: 8px;
    max-width: 500px;
}

.title {
    font-weight: 400;
    font-size: 48px;

    & :first-child {
        font-size: 80px;
    }
}

.titleBold {
    font-weight: 900;
}